import Vue from "vue";
import VueRouter from "vue-router";
import Main from "../views/Main.vue";
import Mint from "../views/Mint.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Main,
  },
  {
    path: "/mint",
    name: "Mint",
    component: Mint,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
